import React from 'react';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import {Card, CardTitle} from 'material-ui/Card';
// import {Card, CardActions, CardHeader, CardMedia, CardTitle, CardText} from 'material-ui/Card';
// import Paper from 'material-ui/Paper';
import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';
import Done from 'material-ui/svg-icons/action/done';
import DjangoCSRFToken from 'django-react-csrftoken'

const style = {
  height: 100,
  width: 200,
  margin: 20,
  paddingBottom: 50,
  paddingLeft: 50,
  paddingRight: 50,
  textAlign: 'center',
  display: 'inline-table',
};

class LoginForm extends React.Component {
  
  constructor(props) {
    super(props);
    this.state = {
      username: '',
      password: '',
    }
    this.handleChange = this.handleChange.bind(this);
  }
  
  handleChange(event) {
    // onChange Event logic
    const target = event.target;
    const value = target.value;
    const name = target.name;
    
    this.setState({ [name]: value});
  }
  
  render() {
    const submitEnabled = this.state.username.length > 0 && this.state.password.length > 0;
      return (
        <MuiThemeProvider>
            <Card style={style} zDepth={2}>
              <CardTitle title="Login" subtitle="GO Productiviteit Analyse" />
              <form method="post" action="">
                <DjangoCSRFToken/>
                  <TextField 
                    name="username" 
                    type="text" 
                    hintText="Gebruikersnaam"
                    onChange={this.handleChange} />
                    <p></p>
                  <TextField 
                    name="password" 
                    type="password" 
                    hintText="Wachtwoord"
                    onChange={this.handleChange} />
                    <p></p>
                    <p disabled={submitEnabled}><small>Voer gebruikersnaam en wachtwoord in</small></p>
                <RaisedButton 
                  type="submit"
                  disabled={!submitEnabled}
                  labelPosition="before"
                  primary={true}
                  icon={<Done  />} 
                  >Submit</RaisedButton>
              </form>
            </Card>
        </MuiThemeProvider>
      );
    }
}


export default LoginForm;


